import "./styles.css";

// Question: FizzBuzz - create a function that takes an array,
// replaces all numbers that are divisible by 3 with the
// string Fizz and any number that is divisible by 5 should be
// replaced by the string Buzz.
// So an array with the following: [1,4,6,7,10,15]
// would become [1,4,"Fizz",7,"Buzz","FizzBuzz"]

const question = [1, 4, 6, 7, 10, 15];
// const expected = [1, 4, "Fizz", "foo", "Buzz", "FizzBuzz"];

const ruleSet = [
  {
    3: "Fizz"
  },
  {
    5: "Buzz"
  },
  {
    7: "Foo"
  }
];

const parseArr = (arr = [], ruleSet = []) => {
  const newArr = [];

  arr.map(function myfunc(i) {
    if (!i) return false;

    let newWord = "";

    ruleSet.map((j) => {
      if (i % Object.keys(j)[0] === 0) {
        const wordPart = Object.values(j)[0];

        newWord += wordPart;
      }

      return false;
    });

    newArr.push(`${newWord || i}, `);

    return false;
  });

  return newArr;
};

export default function App() {
  return (
    <div className="App">
      <h1>Hello CodeSandbox</h1>
      <h2>{parseArr(question, ruleSet)}</h2>
    </div>
  );
}
